.container {
  height: 100%;
  display: grid;
  grid-template: 40px auto 40px/ repeat(12, 1fr);
  grid-template-areas:
    "h h h h h h h h h h h h"
    "m m m c c c c c c c c c"
    "f f f f f f f f f f f f";
}

.my-profile-photo {
  width: 30%;
}

.navBar {
  grid-area: h;
}

.menu {
  grid-area: m;
}

.container-article{
  grid-area: c;
}

.footer {

  grid-area: f;
}

